import * as React from 'react'
import {langbar, langbar__list, langbar__item, active} from './langbar.module.scss'

const Langbar = () => {
  return (
    <nav className={langbar}>
                    <ul className={langbar__list}>
                        <li className={langbar__item}>
                            <a href="#" className={active}>RO</a>
                        </li>
                        <li className={langbar__item}>
                            <a href="#">RU</a>
                        </li>
                    </ul>
                </nav>
  )
}

export default Langbar