import * as React from 'react'
import {header, header__container, _container, header__logo, contact, header__navbar, header__langbar} from './header.module.scss'
import Navbar from './navbar/navbar.jsx'
import Logo from './images/logo.png'
import Langbar from './langbar/langbar'

const Header = () => {
  return (
    <header className={header}>
      <div className={header__container}>
        <a href="#" className={header__logo}><img src={Logo} alt=''/></a>
				<div className={header__langbar}>
					<Navbar className={header__navbar}></Navbar>
					<Langbar></Langbar>
					<a href='tel:+37360007980' className={contact}>+373 60 007 980</a>
				</div>
				
      </div>
    </header>
  )
}

export default Header