import * as React from 'react'
import Image from './images/skoda.jpg'
import {ibg, mainblock__footer,welcome__greeting,welcome__title, welcome__category, mainblock__btn,welcome, mainblock__arrow, btn_animate} from './main-bg.module.scss'

const MainBg = () => {
  return (
    <section className={ibg} id="main">
			<article className={welcome}>
				<p className={welcome__greeting}>Bine ati venit pe site-ul nostru!</p>
				<h1 className={welcome__title}>Scoala Auto UTM</h1>
				<p className={welcome__category}>Instruirea conducatorilor auto categoria „A”, „B”, „C”, „D”, „E” <br /> inclusiv si subcategoriile acestora</p>
			</article>
      <div className={btn_animate}>
				<div className={mainblock__footer}>
        <a href="#grupe" className={mainblock__btn}><span>afla mai multe</span></a>
        <a href="#grupe" className={mainblock__arrow}></a>
      </div>
			</div>
			
			<img src={Image} alt=""/>
    </section>
  )
}

export default MainBg