import * as React from 'react'
//import { Link } from 'gatsby'
import {menu, menu__list, menu__item, menu__link} from './navbar.module.scss'

const Navbar = () => {
  return (
    <nav className={menu}>
                    <ul className={menu__list}>
                        <li className={menu__item}>
                            <a href="#main" className={menu__link}><span>Principala</span></a>
                        </li>
                        <li className={menu__item}>
                            <a href="#grupe" className={menu__link}><span>Grupe</span></a>
                        </li>
												<li className={menu__item}>
                            <a href="#categorii" className={menu__link}><span>Categorii</span></a>
                        </li>
                        <li className={menu__item}>
                            <a href="#conditii" className={menu__link}><span>Conditii</span></a>
                        </li>
                        <li className={menu__item}>
                            <a href="#gallery" className={menu__link}><span>Galerie</span></a>
                        </li>
												<li className={menu__item}>
                            <a href="#util" className={menu__link}><span>Util</span></a>
                        </li>
												<li className={menu__item}>
                            <a href="#contacts" className={menu__link}><span>Contacte</span></a>
                        </li>
                    </ul>
                </nav>
  )
}

export default Navbar