import * as React from 'react'
import {categorii, categorii__subtitle, categorii__text, categorii__title, categorii__container, categorii__formare, categorii__table} from './categorii.module.scss'

const Categorii = () => {
  return (
    <section className={categorii} id="categorii">
			<div className={categorii__container}>
				<h2 className={categorii__title}>Informatii privind categoriile si subcategoriile propuse</h2>
				<div className={categorii__formare}>
					<div className={categorii__table}>
						<table>
						<thead>
							<tr>
								<th>Categoria</th>
								<th>Descrierea</th>
								<th>Varsta minima pentru obtinerea categoriei</th>
								<th>Durata de studii (luni)</th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>AM</td>
								<td className={categorii__text}>Mopede (motorete), adica vehicule cu doua roti sau vehicule cu trei roti cu o viteza maxima constructiva de pina la 45 km/h si avind urmatoarele caracteristici: (1) in cazul vehiculului cu doua roti, un motor a carui: - cilindree nu depaseste 50 cm cub, in cazul unui motor cu ardere interna sau - putere nominala continua maxima nu depaseste 4 kW, in cazul unui motor electric; (2) in cazul vehiculului cu trei roti, un motor a carui: - cilindree nu depaseste 50 cm cub, in cazul unui motor cu aprindere prin scinteie sau - putere neta maxima nu depaseste 4 kW, in cazul altor tipuri de motor cu ardere interna sau - putere nominala continua maxima nu depaseste 4 kW, in cazul unui motor electric.</td>
								<td>14</td>
								<td>3</td>
							</tr>
							<tr>
							<td>A1</td>
								<td className={categorii__text}>Motociclu cu capacitatea cilindrica a motorului de pana la 125 cm cub si o putere care nu depaseste 11kW si cu raport de putere/greutate ce nu depaseste 0,1kW/kg si triciclul cu o putere care nu depaseste 15kW.</td>
								<td>16</td>
								<td>3</td>
							</tr>
							<tr>
							<td>A2</td>
								<td className={categorii__text}>Motociclu cu capacitatea cilindrica a motorului de pana la 125 cm cub si o putere care nu depaseste 11kW si cu raport de putere/greutate ce nu depaseste 0,1kW/kg si triciclul cu o putere care nu depaseste 15kW.</td>
								<td>18</td>
								<td>3</td>
							</tr>
							<tr>
							<td>A</td>
								<td className={categorii__text}>Motociclu cu sau fara atas. Triciclu cu o putere care depaseste 15 kW.</td>
								<td>24 <span className={categorii__text}>sau</span> 20 <span className={categorii__text}>avind A2 nu mai putin de doi ani</span></td>
								<td>3</td>
							</tr>
							<tr>
							<td>B1</td>
								<td className={categorii__text}>Cuadricicle motorizate (vehicule cu motor cu patru rot, a caror masa fara incarcatura nu depaseste 400 kg (categoria L7e) <br /> (550 kg pentru vehiculele destinate transportului de marfuri), neincluzind masa bateriilor in cazul vehiculelor electrice si a caror putere neta maxima a motorului nu depaseste 15 kW).</td>
								<td>16</td>
								<td>3</td>
							</tr>
							<tr>
							<td>BC1</td>
								<td className={categorii__text}>Autoturisme de categoria B si autocamioane de categoria C1. (masa maxima autorizata pina la 7500 kg).</td>
								<td>18</td>
								<td>3.5</td>
							</tr>
							<tr>
							<td>BC</td>
								<td className={categorii__text}>Autoturisme de categoria B si autocamioane de categoria C (masa maxima autorizata mai mare de 7500 kg).</td>
								<td>18</td>
								<td>3.5</td>
							</tr>
						</tbody>
					</table>
					</div>
					<div className={categorii__table}>
					<table>
						<thead>
						<tr>
								<th>Categoria</th>
								<th>Descrierea</th>
								<th>Varsta minima pentru obtinerea categoriei</th>
								<th>Durata de studii (luni)</th>
							</tr>
						</thead>
						<tbody>
						<tr>
							<td>B</td>
								<td className={categorii__text}>Autovehicul al carui numar de locuri pe scaune, in afara locului conducatorului, nu este mai mare de 8 locuri si a carui masa totala maxima autorizata nu depaseste 3500 kg; ansamblu format dintr-un autovehicul tragator din categoria B si o remorca a carei masa maxima autorizata nu depaseste 750 kg; ansamblu de vehicule format dintr-un vehicul tragator din categoria B si o remorca a carei masa maxima autorizata este mai mare de 750 kg, dar nu mai mare decit masa proprie a autovehiculului tragator, iar masa totala maxima autorizata a intregului ansamblu nu depaseste 3500 kg.</td>
								<td>18</td>
								<td>3</td>
							</tr>
							<tr>
							<td>B la C1</td>
								<td className={categorii__text}>Autovehicul, altul decit cel din subcategoria D1 sau categoria D, cu masa totala maxima autorizata ce depaseste 3500 kg, dar nu mai mare de 7500 kg, cu un numar de locuri pe scaune nu mai mare de 8, in afara locului conducatorului; ansamblu format dintr-un autovehicul tragator din subcategoria C1 si o remorca a carei masa maxima autorizata nu depaseste 750 kg.</td>
								<td>18</td>
								<td>3</td>
							</tr>
							<tr>
							<td>B la C</td>
								<td className={categorii__text}>Autovehicul, cu exceptia celor din subcategoria D1 sau categoria D, cu masa maxima autorizata mai mare de 3500 kg, cu un numar de locuri pe scaune nu mai mare de 8, in afara locului conducatorului; ansamblu format dintr-un autovehicul tragator din categoria C si o remorca a carei masa maxima autorizata nu depaseste 750 kg.</td>
								<td>21</td>
								<td>3</td>
							</tr>
							
							<tr>
							<td>D1</td>
								<td className={categorii__text}>Autovehicul destinat transportului de persoane, avind mai mult de 8, dar nu mai mult de 16 locuri pe scaune, in afara locului conducatorului, si lungimea maxima nu depaseste 8m; ansamblu format dintr-un autovehicul tragator din subcategoria D1 si o remorca a carei masa totala maxima autorizata nu depaseste 750 kg.</td>
								<td>21</td>
								<td>2</td>
							</tr>
							<tr>
							<td>D</td>
								<td className={categorii__text}>Autovehicul destinat transportului de persoane, avind mai mult de 8 locuri pe scaune, in afara locului conducatorului; ansamblu format dintr-un autovehicul tragator din categoria D si o remorca a carei masa totala maxima autorizata nu depaseste 750 kg.</td>
								<td>24</td>
								<td>2</td>
							</tr>
							<tr>
							<td>E</td>
								<td className={categorii__text}>Ansamblu de vehicule format dintr-un vehicul tragator din categoria B, C, D si o remorca sau semiremorca.</td>
								<td>21</td>
								<td>1.5</td>
							</tr>
						</tbody>
					</table>
					</div>
					
				</div>
				<p className={categorii__subtitle}>* Masa maxima autorizata – masa totala a vehiculului echipat, inclusiv incarcatura, soferul si pasagerii, indicata de catre intreprinderea producatoare ca maxim admisibila. Masa maxima autorizata a ansamblului de vehicule este suma maselor maxime autorizate ale fiecarui vehicul din care este formata garnitura auto.</p>
			</div>
			
		</section>
  )
}

export default Categorii