import * as React from 'react'
import image from './images/dev.svg'
import {footer,footer__copyright, footer__container, footer__dev, footer__text, footer__icon} from './footer.module.scss'

const Footer = () => {
  return (
    <section className={footer}>
			<div className={footer__container}>
				<p className={footer__copyright}>© 2023. All Rights Reserved</p>
			<a href="https://t.me/Vartag90" target='_blank' rel="noreferrer" className={footer__dev}>
        <span className={footer__text}>Handcrafted by</span>
        <span className={footer__icon}><img src={image} alt="author_logo"/></span>
      </a>
			</div>
		</section>
  )
}

export default Footer